.wrapper {
    display: flex;
    justify-content: center;
}

.calculator {
    background: var(--bg-lite-color);
    padding: 20px;
    border: .15rem solid var(--border-color);
    border-radius: 1rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    margin-top: 10vh;
}
.calculator_wrapper {
    display: inline-block;
}
input {
    height: 3rem;
    width: 100%;
    background: rgba(234, 207, 170, 0.5);
    border: 2px solid var(--border-color);
    border-radius: .5rem;
    font-size: 18px;
    outline: none;
    padding: 0 .3rem;
}

.buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
}

.buttons_line {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
}

.button {
    /*padding: 10px;*/
    background: var(--bg-color);
    width: 3rem;
    height: 3rem;
    font-size: 26px;

    border-radius: .3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: .2rem;
    color: var(--text-color);
    cursor: pointer;
    border: 1px solid rgba(140,129,101,0.52);
}

.button.special_sign {
    background: rgba(16, 51, 103, 0.19);
}

.button.equally {
    background: rgba(49, 108, 14, 0.29);
}
.button.clear_btn {
    background: rgba(204, 55, 29, 0.29);
    height: 50%;
}