.nav_bar {
    padding: 10px 20px;
    margin-right: auto;
    margin-left: auto;
    /*width: 20vw;*/
    display: flex;
    /*align-items: center;*/
    /*justify-content: center;*/
    background: burlywood;
    border-radius: 0 0 10px 10px;
}
.nav_bar_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav_bar a {
    padding: .3rem .5rem;
    background: var(--bg-lite-color);
    /*background: var(--bg-color);*/
    color: var(--text-color);
    text-decoration: none;
    font-size: 1.5rem;

    border-radius: 10px 0 0 10px;

    transition: all linear 0.3s;
}
.nav_bar a:nth-child(2) {
    border-radius: 0 10px 10px 0;
    border-right: none;
}
@media (max-width: 900px) {
    .nav_bar a {
        font-size: 1.8rem;
        padding: .5rem;
    }
}
