.btn {
    /*width: 100px;*/
    /*height: 100px;*/
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    background: var(--bg-color);
    border: 2px solid var(--border-color);
    font-size: 20px;
    color: var(--text-color);
    cursor: pointer;
}
.btn_wrapper {
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}
.clicks_count {
    text-align: center;
    /*font-size: 30px;*/
    color: rgba(0, 0, 0, 0.75);
    margin-top: 20vh;
    font-size: 3rem;
}
.reset {
    background: var(--bg-color);
    border: 2px solid var(--border-color);
    padding: 5px 10px;
    border-radius: .5rem;
    font-size: 20px;
}
.reset_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20vh;
}